import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';

import { LoopBackConfig } from '../../../shared/sdk';

declare const lbclient: any;

import { LogServiceProvider } from '../../providers/log-service/log-service';
import { PdaparamsServiceProvider } from '../../providers/pdaparams-service/pdaparams-service';
import { ConfigServiceProvider } from '../../providers/config-service/config-service';
import { ConnectivityMonitorProvider } from '../../providers/connectivity-monitor/connectivity-monitor';
import { SharedServiceProvider } from '../../providers/shared-service/shared-service';

/**
 * Generated class for the ChangePage page.
 *
 * See http://ionicframework.com/docs/components/#navigation for more info
 * on Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-change',
  templateUrl: 'change.html',
})
export class ChangePage {

  logParams = { site: this.pdaParams.getSiteId(), driver: this.pdaParams.getDriverId(), fn: 'ChangePage' };
  log = this.logger.getInstance(this.logParams);
  RemoteJob = lbclient.models.Job;
  Job = lbclient.models.LocalJob;
  changes = 0;
  diff;
  show_this = true;
  jobs;
  sync = lbclient.sync;
  isOnline;
  loginFlag = this.pdaParams.isDrvLoggedOn();
  constructor(public navCtrl: NavController, public navParams: NavParams, private pdaParams: PdaparamsServiceProvider,
    private logger: LogServiceProvider, private configService: ConfigServiceProvider,
    private conMonService: ConnectivityMonitorProvider, private sharedService: SharedServiceProvider) {
    LoopBackConfig.setBaseURL(this.configService.apiURL);
    LoopBackConfig.setApiVersion(this.configService.API_VERSION);
    this.conMonService.isConnected.subscribe(res => {
      this.sharedService.isOnline = res;
    });
  }

  ionViewDidLoad() {
    this.Job.getChangeModel().find((err, changes) => {
      this.changes = changes;
      this.RemoteJob.diff(0, changes, (err, diff) => {
        this.diff = diff;
      });
    });

    this.Job.find((err, jobs) => {
      this.jobs = jobs;
    });
  }

  ionViewWillEnter() {
    //set the tab string in shared-service
    this.sharedService.tab = "Change Page";
  }

  clearLocalStorage = () => {
    localStorage.removeItem('osc-local-db');
    this.jobs = {};
  };

  logLocalStorage = () => {
    var logmsg = { type: '', data: '' };
    logmsg.type = 'logLocalStorage';
    logmsg.data = JSON.parse(localStorage.getItem('osc-local-db'));
    this.log.info(logmsg);
  };

  connected = () => {
    return this.sharedService.isOnline;
  }; 
}



